	module.exports = {"data":{"markdownRemark":{"html":"<p>Public disclosure enables programs to be transparent about the security vulnerabilities found for their program. HackerOne's public disclosure process balances transparency with control over what information is shared with the public.  </p>\n<p>Programs can choose from 3 disclosure settings:</p>\n<table>\n<thead>\n<tr>\n<th>Option</th>\n<th>Detail</th>\n</tr>\n</thead>\n<tbody>\n<tr>\n<td>Disclosure by Default</td>\n<td>You or the security team can request public disclosure for any closed report in the program. If the admin of the program agrees to disclosure, the contents of the report will be made public within 30 days.\n<br>\n \n<br>\n<em>This is the default setting for all verified programs</em>\n.</td>\n</tr>\n<tr>\n<td>Disclosure requiring Mutual Agreement</td>\n<td>You can request public disclosure for any closed report in the program. If the program security team agrees to disclosure, the contents of the report will be made public. If the security team doesn't take any action, the contents of the report will remain private. \n<br>\n<em>The program must request to opt-in to this option.</em></td>\n</tr>\n<tr>\n<td>Disclosure Disabled</td>\n<td>Public disclosure isn't allowed for any report.</td>\n</tr>\n</tbody>\n</table>\n<h3>Requesting Public Disclosure</h3>\n<p>Both you and program members can request public disclosure. To request public disclosure:</p>\n<ol>\n<li>\n<p>Go to the report you want to publicly disclose. </p>\n</li>\n<li>\n<p>Make sure the report is closed. </p>\n</li>\n<li>\n<p>Select <strong>Request public disclosure</strong> in the action picker at the bottom of the report. </p>\n</li>\n<li>\n<p>Select whether you want to disclose the <strong>Full</strong> report or <strong>Limited</strong>. </p>\n<p>Option | Details\n------ | -------\nFull | Upon public disclosure, the full contents of the report are visible including:<ul>Vulnerability information</ul><ul>Summary</ul><ul>Timeline such as comments and attachment</ul><br><em>Note: Internal comments are hidden.</em><br>\nLimited | Only the summary and timeline of the activity are visible. All comments and attachments are hidden. Limited disclosure allows for greater control over sensitive or extraneous information. </p>\n</li>\n<li>\n<p><em>(Optional)</em> Enter a comment to describe your reasons for public disclosure. </p>\n</li>\n<li>\n<p>Click <strong>Post</strong>.    </p>\n</li>\n</ol>\n<p><img src=\"./images/public-disclosure-2.png\" alt=\"public-disclosure-2\"></p>\n<p>After public disclosure has been requested, the admin of the of the program can choose to publicly disclose the report. They can select <strong>Disclose publicly</strong> to disclose the report. </p>\n<p><img src=\"./images/public-disclosure-3.png\" alt=\"public-disclosure-3\"></p>\n<h3>Limiting Public Report Information</h3>\n<p>When publishing reports, the security team can choose to disclose the report in full or limit the information published. The default is to display all the communication between the hacker and the security team from first report to resolution. There are 2 times the security team can choose to limit information published in a report: </p>\n<ul>\n<li>At the time they disclose the report</li>\n<li>After the report has been made public</li>\n</ul>\n<p>There are 2 ways a security team can limit the information shared: </p>\n<table>\n<thead>\n<tr>\n<th>Option</th>\n<th>Detail</th>\n</tr>\n</thead>\n<tbody>\n<tr>\n<td>Redacting Sensitive Information</td>\n<td>You can redact information in your report. In the \n<strong>Visibility</strong>\n field in your report, select \n<strong>Redact</strong>\n. This will enable you to provide the string of words you permanently want to redact from your report. \n<br>\n<img src=\"./images/public-discloure-4.png\" alt=\"public-disclosure-4\"></td>\n</tr>\n<tr>\n<td>Limiting Visibility</td>\n<td>Only the summary and timeline of the report are visible and all comments and attachments are hidden. You can limit visibility when requesting public disclosure.</td>\n</tr>\n</tbody>\n</table>\n<p>Here's a good example of a summarized disclosure from the Shopify security team: <a href=\"https://hackerone.com/reports/64164\">https://hackerone.com/reports/64164</a>.</p>\n<p>This diagram illustrates HackerOne's public disclosure process: </p>\n<p><img src=\"./images/public-disclosure.png\" alt=\"public-disclosure\"></p>\n<p>For more information, please read the full <a href=\"https://hackerone.com/disclosure-guidelines\">HackerOne Disclosure Guidelines</a>.  There is a lengthier blog post on <a href=\"https://hackerone.com/blog/public-disclosure-on-hackerone?utm_source=h1&#x26;utm_medium=help&#x26;utm_campaign=hdpdw\">Public Disclosure</a> as well.  If disclosure was accidentally initiated or you have concerns about this process, please <a href=\"https://support.hackerone.com/hc/en-us/requests/new\">submit a support request</a>.</p>","frontmatter":{"path":"/hackers/public-disclosure","title":"Public Disclosure"}}},"pathContext":{}}

